import { Component, OnInit, Output, EventEmitter, Input, ViewChild, TemplateRef, SimpleChanges, OnChanges, Injector, HostBinding, Renderer2, ElementRef, ChangeDetectorRef } from '@angular/core';
import { DatagridComponent } from '@farris/ui-datagrid';
import { NotifyService } from '@farris/ui-notify';


export interface EnumData {
    id: any;
    value: string;
    name: string;
}

@Component({
    selector: 'ide-enum-editor',
    template: `
    <div class="border-bottom mb-1 py-1">
        <button class="btn btn-primary mr-1 ml-2" (click)="add()"> æ°å¢</button>
        <button class="btn btn-default mr-1" (click)="remove()">å é¤</button>
    </div>
    <div class="d-flex flex-column flex-fill">
        <farris-datagrid #dg [idField]="'id'"
            [columns]="columns" [sizeType]="'md'" [showAllCheckbox]="true"
            [striped]="false" [showCheckbox]="true" [multiSelect]="true"
            [data]="value" [fit]="true" [fitColumns]="true"
            [showBorder]="true" [pagination]="false"
            [selectOnCheck]="false" [checkOnSelect]="false"
        ></farris-datagrid>

        <ng-template #nameTemp let-ctx>
            <div class="f-datagrid-cell-formgroup farris-group-auto">
                <input type="text" class="form-control" name="name" [(ngModel)]="ctx.rowData['name']">
            </div>
        </ng-template>
        <ng-template #valueTemp let-ctx>
            <div class="f-datagrid-cell-formgroup farris-group-auto">
                <input type="text" class="form-control" [(ngModel)]="ctx.rowData['value']" name="value">
            </div>
        </ng-template>

        <ng-template #activeTemp let-ctx>
            <button type="button" class="btn btn-warning  mr-1" (click)="deleteItem(ctx.rowData.id)">å é¤</button>
            <button class="btn btn-info mr-1" style="padding: 2px 5px" (click)="moveTop($event, ctx.rowData.id, ctx.rowIndex)" title="ç½®é¡¶">
                <span class="f-icon f-icon-roofing"></span>
            </button>
            <button class="btn btn-info mr-1" style="padding: 2px 5px" (click)="moveUp($event, ctx.rowData.id, ctx.rowIndex)" title="ä¸ç§»">
                <span class="f-icon f-icon-arrow-chevron-up"></span>
            </button>
            <button class="btn btn-info mr-1"  style="padding: 2px 5px" (click)="moveDown($event, ctx.rowData.id, ctx.rowIndex)" title="ä¸ç§»">
                <span class="f-icon f-icon-arrow-chevron-down"></span>
            </button>
            <button class="btn btn-info mr-1" style="padding: 2px 5px" (click)="moveBottom($event, ctx.rowData.id, ctx.rowIndex)" title="ç½®åº">
                <span class="f-icon f-icon-bottomsetting"></span>
            </button>
        </ng-template>

    </div>
    <ng-template #enumFooter>
        <div class="btns">
            <button type="button" class="btn btn-outline-secondary btn-sm px-3" (click)="clickCancel()" > åæ¶ </button>
            &nbsp;&nbsp;
            <button  type="button"  class="btn btn-outline-primary btn-sm px-3" (click)="clickConfirm()" > ç¡®å® </button>
        </div>
    </ng-template>
    `,
    styles: []
})
export class IdeEnumEditorComponent implements OnInit, OnChanges {
    @Output() closeModal = new EventEmitter<any>();
    // æ¨¡ææ¡ç¡®å®åå³é­ï¼å¹¶ä¼ éåæ°
    @Output() submitModal = new EventEmitter<any>();
    @Input() value: EnumData[]; // æä¸¾æ°ç»
    @Input() editorParams: any = {};

    @ViewChild('enumFooter') modalFooter: TemplateRef<any>;

    @ViewChild('dg') dg: DatagridComponent;
    @ViewChild('nameTemp') nameTemp: TemplateRef<any>;
    @ViewChild('valueTemp') valueTemp: TemplateRef<any>;
    @ViewChild('activeTemp') activeTemp: TemplateRef<any>;

    @Output() valueChange = new EventEmitter();

    modalConfig = {
        title: 'æä¸¾ç¼è¾å¨',
        width: 900,
        height: 500,
        showButtons: true
    };

    columns = [];

    constructor(
        private notifyService: NotifyService,
        private injector: Injector,
        private render: Renderer2,
        private el: ElementRef,
        private cd: ChangeDetectorRef
    ) { }

    ngOnInit() {

        this.columns = [
            { field: 'name', title: 'åç§°', width: 100, template: this.nameTemp },
            { field: 'value', title: 'å¼', width: 100, template: this.valueTemp },
            { title: 'æä½', width: 100, template: this.activeTemp, halign: 'center', align: 'center' }
        ];

        if (!this.value || this.value.length === 0) {
            this.value = [];
        } else {
            this.value = this.value.map((n, i) => {
                return {
                    id: i + 1,
                    ...n
                };
            });
        }


        this.render.addClass(this.el.nativeElement, 'f-utils-absolute-all');
        this.render.addClass(this.el.nativeElement, 'f-utils-flex-column');

    }

    ngOnChanges(changes: SimpleChanges) {

    }

    private map2EnumData() {
        return this.value.map(v => {
            return {
                name: v.name,
                value: v.value
            };
        });
    }

    private changeEmit() {
        this.cd.detectChanges();
        this.valueChange.emit(this.value);
    }

    clickCancel() {
        this.closeModal.emit();
    }

    clickConfirm() {
        if (!this.checkBeforeSave()) {
            return;
        }
        const enumData = this.map2EnumData();
        this.submitModal.emit({ value: enumData });
    }

    /**
     * ä¿å­åæ£æ¥
     */
    checkBeforeSave(): boolean {

        const msg = {
            type: 'warning',
            title: 'ç³»ç»æç¤º',
            msg: 'è¯·æ·»å æä¸¾å¼ã',
            timeout: 3000
        };


        // â  ç©º
        if (!this.value || this.value.length === 0) {
            this.notifyService.warning(msg);
            return false;
        }
        // â¡ éç©ºï¼åæ ¡éªæ¯ä¸ªçé®å¼æ¯å¦ä¸ºç©ºï¼
        for (const item of this.value) {
            if (!item.name || !item.value) {
                msg.msg = 'æä¸¾å¼åæä¸¾åç§°åä¸åè®¸ä¸ºç©ºã';
                this.notifyService.warning(msg);
                return false;
            }
        }
        // â¢ é®ä¸åè®¸éå¤ï¼
        const enumKeys = this.value.map(e => e.value);
        const keySet = new Set(enumKeys);
        const exclusiveKeys = Array.from(keySet);
        if (enumKeys.length !== exclusiveKeys.length) {
            msg.msg = 'æä¸¾å¼ä¸åè®¸éå¤ã';
            return false;
        }

        // â¢ å¼ä¸åè®¸éå¤ï¼
        const enumNames = this.value.map(e => e.name);
        const nameSet = new Set(enumNames);
        const exclusiveNames = Array.from(nameSet);
        if (enumNames.length !== exclusiveNames.length) {
            msg.msg =  'æä¸¾åç§°ä¸åè®¸éå¤ã';
            this.notifyService.warning(msg);
            return false;
        }

        return true;
    }


    add() {
        this.value = [ ...this.value, {  id: (this.value.length + 1), name: '', value: ''  } ];
        this.changeEmit();
    }

    remove() {
        const rows = this.dg.checkeds;
        if (rows && rows.length) {
            this.value = this.value.filter( v => {
                return rows.findIndex(r => r.id === v.id) === -1;
            });
            this.changeEmit();
        } else {
            this.notifyService.warning('è¯·é©éè¦å é¤çæ°æ®ï¼');
        }
    }

    deleteItem(id) {
        this.value = this.value.filter( v => {
            return v.id !== id;
        });
        this.changeEmit();
    }

    moveTop($event: MouseEvent, id, index) {
        $event.stopPropagation();
        const i = this.value.findIndex(n => n.id === id);
        // const i = index;
        this.value.unshift(this.value[i]);
        this.value.splice(i + 1, 1);

        this.value = [...this.value];
        this.changeEmit();
        return false;
    }

    moveBottom($event, id, index) {
        $event.stopPropagation();
        const i = this.value.findIndex(n => n.id === id);
        // const i = index;
        this.value.push(this.value[i]);
        this.value.splice(i, 1);
        this.value = [...this.value];
        this.changeEmit();
        return false;
    }

    moveUp($event, id, index) {
        $event.stopPropagation();
        const i = this.value.findIndex(n => n.id === id);
        // const i = index;
        if (i !== 0) {
            const tempArr = this.value.splice(i, 1);
            this.value.splice(i - 1, 0, ...tempArr);
            this.value = [...this.value];
            this.changeEmit();
        }
        return false;
    }

    moveDown($event, id, index) {
        $event.stopPropagation();
        const len = this.value.length;
        const i = this.value.findIndex(n => n.id === id);
        // const i = index;
        if (i !== len - 1) {
            const tempArr = this.value.splice(i, 1);
            this.value.splice(i + 1, 0, ...tempArr);
            this.value = [...this.value];
            this.changeEmit();
        }
        return false;
    }
}
